.overlay {
	background: rgba(0, 0, 0, 0.6);
	cursor: default;
	opacity: 0;
	position: fixed;
	bottom: 0;
	right: 0;
	left: 0;
	top: 0;
	visibility: hidden;
	z-index: 99999;
	transition: opacity 0.3s;
	will-change: opacity;
}

.popup-f {
	width: rem(400);
	min-height: rem(605);
	margin: auto;
	display: flex;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	opacity: 0;
	position: fixed;
	visibility: hidden;
	z-index: 999999;
	transition: opacity 0.2s;
	background: rgb(110,203,218);
background: -webkit-linear-gradient(rgba(110,203,218,1) 0%, rgba(26,147,166,1) 100%);
background: -o-linear-gradient(rgba(110,203,218,1) 0%, rgba(26,147,166,1) 100%);
background: linear-gradient(rgba(110,203,218,1) 0%, rgba(26,147,166,1) 100%);
	border-radius: 1px;
	overflow: hidden;
	will-change: opacity;
	box-shadow: -1px 2px 7px 3px rgba(0,0,0,0.2);
	@include mobile {
		min-height: 31.5rem;
	}
}

.fofm__wr {
	width: 100%;
	padding: rem(3) rem(20) rem(7) rem(20);
}

.popup-f .close-modal {
	position: absolute;
	top: rem(9);
	right: rem(9);
	@include size(rem(40));
	z-index: 20;
}

.close-svg {
	@include size(rem(40));
	color: $primary-color;
	fill: $primary-color;
	stroke-width: 14px;
}

.fofm-title {
	font-size: rem(36);
	font-weight: $font-weight-bold;
	color: $primary-color;
	text-align: left;
	margin-bottom: rem(10);
	line-height: 1.65;
}

.popup-mes {
	width: rem(350);
	min-height: rem(200);
	margin: auto;
	display: flex;
	align-items: center;
	top: 50%;
	left: 50%;
	opacity: 0;
	position: fixed;
	visibility: hidden;
	z-index: 999999;
	transform: translate(-50%, -50%);
	transition: opacity 0.2s;
	background-color: $primary-color;
	border-radius: rem(5);
	overflow: hidden;
	will-change: opacity;
	box-shadow: -1px 2px 7px 3px rgba(0,0,0,0.2);
	@include tablet {

	}
}

.popup-mes .window {
	width: 100%;
	height: 100%;
	padding: 0 27px 27px;
	text-align: center;
}

.popup-mes .close-mes {
	position: absolute;
	top: rem(9);
	right: rem(9);
	@include size(rem(20));
	z-index: 20;
	& .closemes-svg {
		@include size(rem(20));
		color: $orange-color;
		fill: $orange-color;
		stroke-width: 24px;
	}
}

.fofm__text {
	width: 100%;
    min-height: 2rem;
    font-size: rem(18);
    text-align: center;
    line-height: 1.35;
    margin-bottom: rem(20);
    color: $primary-color;
    font-weight: $font-weight-light;
    text-align: left;
    margin-top: rem(20);
}

.insText__title {
	margin-bottom: rem(20);
    font-size: 1.3rem;
    color: $orange-color;
}

.insText__text {
	line-height: 1.67;
	font-weight: bold;
}


